.todo-main{
    position: relative;
    width: 100%;
    height: 100%;
}
.todo-list{
    position: relative;
    margin: 0;
    padding: 0;
    height: 100%;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
    cursor: pointer;
}
.todo-list-last{
    visibility: hidden;
    margin: 0;
    padding: 0;
    height: 100%;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
    cursor: pointer;
}
.todo-li {
    position: relative;
    font-size: 20px;
    width:17%;
    margin-top: 5%;
    margin-left: 4%;
    margin-right: 4%;
    height: 100%;
    border-radius: 9%;
    background-color: #fff;
    -webkit-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.1);
            box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.1);
}
.todo-done{
    opacity: 0.2;
    position: relative;
    font-size: 20px;
    width:17%;
    margin-top: 5%;
    margin-left: 4%;
    margin-right: 4%;
    height: 100%;
    border-radius: 9%;
    background-color: #cc9a9a;
    -webkit-box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.1);
            box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.1);
}
.todo-done::after{
    content: '\5DF2\5B8C\6210';
    font-size: 30px;
    color: #000;
    font-weight: bolder;
}
.view-todo {
    margin-top: 10px;
}
.check-input{
    border: none;
    text-align: center;
    width: 40px;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    opacity: 0;
}
label {
    word-break: break-all;
    padding: 20px 10px 10px 10px;
    display: inline-block;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
    color: #cc9a9a;
    font-weight:500;
}
.remove-button{
    display:none;
    position: absolute;
    width:40px;
    height: 40px;
    top: 0;
    right: 40%;
    bottom: -50px;
    margin: auto 0;
    font-size: 30px;
    color: #cc9a9a;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    margin-bottom: 11px;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
}
.iconfont-delete:hover{
    color: #af5b5e;
}
.todo-li:hover .remove-button{
    display: block;
}
.todo-last{
    width:100%;
    height: 30%;
    margin-top: 2%;
}
